body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.result {
  margin-left: 50px;
  margin-right: 50px;
  padding-top: 20px;
  border-top: 2px solid #ddd;

}

.wrapp {
  border-radius: 10px;
  padding: 20px;
  width: 20vw;
  background-color: rgba(0, 0, 0, 0.5);
}

.result h3,
.result h4 {
  font-weight: normal;
  color: white;
}

.result em {
  font-weight: bold;
}
.App {
  background-image: url(/weather-app/static/media/sky-690293_1920.1aacd0d3.jpg);
  background-position: center;
  background-size: 100%;
  /* width: 100%; */
  height: 100vh;

}

.App input,
.App button {
  margin: 20px;
  padding: 10px 20px;
  border: 2px solid gray;
  background-color: #fff;
  transition: .3s;

}

form {
  display: flex;
  justify-content: center;
}

.App button {
  cursor: pointer;
}

.App button:hover {
  background-color: #000;
  color: white;
  border-color: black;
}
